*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul, ol, dl {
    list-style: none;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.container-big {
    width: 100%;
    max-width: 1460px;
    height: auto;
    margin: 0 auto;
    padding: 0 30px;
}

.container-small {
    width: 100%;
    max-width: 1200px;
    height: auto;
    margin: 0 auto;
    padding: 0 30px;
}

.page__body {
    max-width: 100%;
    min-width: @mobile-width;
    margin: 0;
    padding: 0;

    font-family: 'Roboto', 'Arial', sans-serif;
    font-size: 16px;
    line-height: 18.75px;
    font-weight: 300;
    color: #fff;

    background-color: #1B1B22;
}

.page__button {
    position: relative;

    width: 250px;
    height: 72px;
    padding: 10px;

    .montserrat;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    text-transform: uppercase;

    background: linear-gradient(180deg, #ECBB87 0%, #FFCC9A 100%);
    box-shadow: 0px 14px 46px rgba(241, 187, 135, 0.3), inset 0px 0px 28px rgba(255, 255, 255, 0.3), inset 0px -4px #CF9376;
    border-radius: 15px;
    border: none;

    cursor: pointer;
    overflow: hidden;
    transition: all 0.4s ease;

    &::before {
        content: "";

        position: absolute;
        top: -35%;
        left: 46%;

        width: 68%;
        height: 69%;

        border-radius: 50%;

        background: #F0DDC9;
        filter: blur(30.5px);
    }

    &::after {
        content: "";

        position: absolute;
        top: 35%;
        left: -10%;

        width: 39%;
        height: 86%;

        border-radius: 50%;

        background: #FFE3BF;
        filter: blur(30.5px);
    } 

    &:hover {
        box-shadow: 0px 14px 60px 15px rgba(241, 187, 135, 0.3), inset 0px 0px 28px rgba(255, 255, 255, 0.3), inset 0px -4px #CF9376;
    }

    &:active {
        box-shadow: 0px 14px 34px rgba(241, 187, 135, 0.3), inset 0px 0px 28px rgba(255, 255, 255, 0.3), inset 0px -4px #CF9376;
        opacity: 0.8;
    }
}

.phone-link {
    display: block;

    .neumann;
    font-size: 20px;
    line-height: 29px;
    text-align: right;
    text-decoration: none;

    background-image: linear-gradient(180deg, #FFFFFF 0%, #FFCC9A 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.phone-icon {
    position: relative;

    display: block;
    width: 26px;
    height: 26px;

    border-radius: 50%;

    background: url('../img/icon-phone.svg') no-repeat 50% 50%/10px 10px,
                linear-gradient(180deg, #FFFFFF 0%, #FFCC9A 100%);
    box-shadow: 0px 14px 46px rgba(241, 187, 135, 0.3);

    &::before {
        content: "";

        position: absolute;
        top: 50%;
        left: 50%;

        width: 36px;
        height: 36px;

        border-radius: 50%;

        background: linear-gradient(180deg, #FFFFFF 0%, #FFCC9A 100%);
        opacity: 0.2;
        box-shadow: 0px 14px 46px rgba(241, 187, 135, 0.3);

        transform: translate(-50%, -50%);
    }
}